import { useTheme } from '@material-ui/core/styles'
import useMediaQuery from '@material-ui/core/useMediaQuery'

// use md to detect mobile screen-size, equivalent 960px
// ref https://material-ui.com/customization/breakpoints/
// ref https://material-ui.com/components/use-media-query/#using-material-uis-breakpoint-helpers
const useMobileQuery = (): boolean => {
    const theme = useTheme()
    // screen size above 960px will not consider mobile
    const aboveMobileScreen = useMediaQuery(theme.breakpoints.up('md'))
    return !aboveMobileScreen
}
export default useMobileQuery