import CssBaseline from '@material-ui/core/CssBaseline'
import Fab from '@material-ui/core/Fab'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles'

import useMobileQuery from '../hooks/useMobileQuery'
import Header from './Header'
import Menu from './Menu'
import ScrollTop from './ScrollTop'
import { useState } from 'react'
import { useEffect } from 'react'

interface LayoutProps {
    children: React.ReactElement
}

const menuWidth = 240;
const anchorId = "back-to-top-anchor"

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            display: 'flex',
            minHeight: '100vh',
        },
        main: {
            flexGrow: 1,
            width: `calc(100% - ${menuWidth}px)`,
            display: 'flex',
            flexDirection: 'column'
        },
        content: {
            backgroundColor: theme.palette.background.default,
            padding: theme.spacing(3),
            flex: 1
        },
        // necessary for content to be below app bar
        toolbar: theme.mixins.toolbar,
        myFooter: {
            backgroundColor: 'rgba(0, 0, 0, 0.1)',
            padding: theme.spacing(3),
        },
    }),
)

const Layout = ({ children }: LayoutProps) => {
    const isMobile = useMobileQuery()
    const [openMenu, setOpenMenu] = useState(true)
    const onOpenMenu = () => setOpenMenu(true)
    const onCloseMenu = () => setOpenMenu(false)

    useEffect(() => {
        if (isMobile) {
            setOpenMenu(false)
        }
    }, [isMobile])

    const classes = useStyles()

    return <div className={classes.root}>
        <CssBaseline />
        <Header
            menuWidth={menuWidth}
            showMenuBtn={isMobile}
            onOpenMenu={onOpenMenu} />
        <Menu
            menuWidth={menuWidth}
            show={openMenu}
            forMobile={isMobile}
            onClose={onCloseMenu} />
        <main className={classes.main}>
            <div className={classes.content}>
                <div className={classes.toolbar} id={anchorId} />
                {children}
                <div>hihi match lg: {isMobile.toString()}</div>
            </div>
            <footer className={classes.myFooter}>
                <span>© 2021 HarleyNTek</span>
            </footer>
        </main>

        <ScrollTop anchorId={anchorId}>
            <Fab color="secondary" size="small" aria-label="scroll back to top">
                <KeyboardArrowUpIcon />
            </Fab>
        </ScrollTop>
    </div>
}

export default Layout