import Drawer from '@material-ui/core/Drawer'
import Divider from '@material-ui/core/Divider'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import InboxIcon from '@material-ui/icons/MoveToInbox'
import MailIcon from '@material-ui/icons/Mail'

import { makeStyles, Theme, createStyles } from '@material-ui/core/styles'

const useStyles = (menuWidth: number) => makeStyles((theme: Theme) =>
    createStyles({
        title: {
            paddingLeft: '16px',
            paddingRight: '16px',
        },
        drawer: {
            [theme.breakpoints.up('md')]: {
                width: menuWidth,
                flexShrink: 0,
            },
        },
        drawerPaper: {
            width: menuWidth,
        },
    }),
)

interface MenuProps {
    menuWidth: number,
    show: boolean,
    forMobile: boolean,
    onClose: () => void
}
const Menu = (props: MenuProps) => {
    const { menuWidth, show, forMobile, onClose } = props
    const classes = useStyles(menuWidth)()
    const closeMenu = (
        event: React.KeyboardEvent | React.MouseEvent,
    ) => {
        if (
            event.type === 'keydown' &&
            ((event as React.KeyboardEvent).key === 'Tab' ||
                (event as React.KeyboardEvent).key === 'Shift')
        ) {
            return;
        }

        onClose();
    }

    const menuContent = <div
        role="presentation"
        onClick={closeMenu}
        onKeyDown={closeMenu}
    >
        <h1 className={classes.title}>HarleyNTek</h1>
        <Divider />
        <List>
            {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
                <ListItem button key={text}>
                    <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
                    <ListItemText primary={text} />
                </ListItem>
            ))}
        </List>
        <Divider />
        <List>
            {['All mail', 'Trash', 'Spam'].map((text, index) => (
                <ListItem button key={text}>
                    <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
                    <ListItemText primary={text} />
                </ListItem>
            ))}
        </List>
    </div>

    return <nav className={classes.drawer}>
        {
            forMobile
                ? <MobileDrawer
                    open={show}
                    onClose={closeMenu}
                    drawerClass={classes.drawerPaper}
                >
                    {menuContent}
                </MobileDrawer>
                : <DesktopDrawer
                    drawerClass={classes.drawerPaper}
                >
                    {menuContent}
                </DesktopDrawer>
        }
    </nav>
}
export default Menu

interface MobileDrawerProps {
    children: React.ReactElement,
    open: boolean,
    onClose?: (event: React.KeyboardEvent | React.MouseEvent) => void,
    drawerClass?: string
}
const MobileDrawer = (props: MobileDrawerProps) => {
    const { children, open, onClose, drawerClass } = props
    const classes = drawerClass ? {
        paper: drawerClass
    } : {}
    return <Drawer
        variant="temporary"
        anchor="left"
        open={open}
        onClose={onClose}
        classes={classes}
        ModalProps={{
            keepMounted: true, // Better open performance on mobile.
        }}
    >
        {children}
    </Drawer>
}

interface DesktopDrawerProps {
    children: React.ReactElement,
    drawerClass?: string
}
const DesktopDrawer = (props: DesktopDrawerProps) => {
    const { children, drawerClass } = props
    const classes = drawerClass ? {
        paper: drawerClass
    } : {}
    return <Drawer
        classes={classes}
        variant="permanent"
        open
    >
        {children}
    </Drawer>
}